.sign-up {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-bottom: 10%;

  .sign-up-form {
    width: max-content;
    height: max-content;
    padding: 40px;
    padding-top: 30px;
    border: 1px solid #ddd;
    border-radius: 10px;
    box-shadow: 0px 40px 60px -15px rgba(13, 16, 37, 0.2);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;

    .sign-up-button {
      width: 9.725rem;
      height: 3rem;
      margin-top: 2rem;
      text-align: center;
      vertical-align: middle;
      line-height: 2.7rem;
      background-color: #fff;
      color: #f53855;
      border: 2px solid #f53855;
      border-radius: 50px;
      font-size: 14px;
      font-weight: 700;
      transition: 0.2s;

      &:hover {
        background-color: #f53838;
        color: #fff;

        cursor: pointer;
      }
    }
  }
}
